/* eslint-disable no-unused-vars */
/* eslint-disable react/jsx-one-expression-per-line */
import React from 'react';
import useGetDate from '../../utils/hooks/useGetData';
import DescriptionWrapper from '../atoms/DescriptionWrapper';
import ImageBox from '../atoms/ImageBox';
import InfoText from '../atoms/InfoText';
import TitleBox from '../atoms/TitleBox';
import SubTitle from '../atoms/SubTitle';
import DetailInfoBox from '../molecules/DetailInfoBox';
import { FUEL_TYPE, SEGMENT } from '../../utils/types';

export default function CarDescription({ ...selected }) {
  const {
    brand,
    name,
    segment,
    fuelType,
    imageUrl,
    amount,
    startDate,
    insurance,
    additionalProducts
  } = selected;

  const { dates, day, month, getdate } = useGetDate(startDate);

  return (
    <DescriptionWrapper>
      <ImageBox alt='car_ImageBox' src={imageUrl} />
      <TitleBox>
        <h6>{brand}</h6>
        <h5>{name}</h5>
        <InfoText> {amount.toLocaleString()} </InfoText>
      </TitleBox>
      <SubTitle>  </SubTitle>
      <DetailInfoBox label='' text={FUEL_TYPE[fuelType]} />
      <DetailInfoBox label='' text={SEGMENT[segment]} />
      <DetailInfoBox
        label=' '
        text={`${month} ${getdate} (${dates[day]})`}
      />
      <SubTitle></SubTitle>
      {insurance.map((el) => (
        <DetailInfoBox key={el.name} label={el.name} text={el.description} />
      ))}
      <SubTitle></SubTitle>
      {additionalProducts.map((el) => (
        <DetailInfoBox
          key={el.name}
          label={el.name}
          text={` ${el.amount.toLocaleString()}`}
        />
      ))}
    </DescriptionWrapper>
  );
}
